import './Header.css';
import { Col, Row, Menu } from 'antd';
// import Icon, { HomeOutlined, VideoCameraFilled, BulbFilled, ToolFilled } from '@ant-design/icons';
import React, { useState, useEffect } from 'react';
import { createFromIconfontCN } from '@ant-design/icons';

import axios from 'axios';
import ServicePath from '../../config/apiUrl.js';
import { useNavigate } from 'react-router-dom'; // react-router


function Header() {

    const [navArray, serNavArray] = useState([

    ]); //will be used to store the menu items and sub-menus
    const navigate = useNavigate();
    useEffect(() => {

        const fetchData = async () => {
            const result = await axios.get(ServicePath.getTypeInfo).then(
                (res) => {
                    // console.log(res.data.data);
                    return res.data.data;
                }
            )

            serNavArray(result)
        };

        fetchData();

    }, []);

    console.log(navArray);

    const handleClik = (e) => {
        if (e.key == 0) {
            console.log(e.key);
            navigate('/bloghome');
        } else if (e.key == 1) {
            navigate('/list');
        } else if (e.key == 2) {
            window.location.href = 'https://www.baidu.com';

        }
    }

    // const items = [
    // {
    //     label: (<a href="/bloghome" target="_blank" rel="noopener noreferrer"></a>),
    //     key: 'Home',
    //     icon: <HomeOutlined />,
    // },
    // {
    //     label: (<a href="/list" target="_blank" rel="noopener noreferrer"></a>),
    //     key: 'Vedio',
    //     icon: <VideoCameraFilled />,
    // },
    // {
    //     label: (
    //         <a href="https://ant.design" target="_blank" rel="noopener noreferrer">
    //             O(_)O~
    //         </a>
    //     ),
    //     key: 'mylife',
    //     icon: <BulbFilled />
    // },
    // {
    //     label: (
    //         <a href="https://ant.design" target="_blank" rel="noopener noreferrer">
    //             
    //         </a>
    //     ),
    //     key: 'myIOT',
    //     icon: <ToolFilled />
    // },
    // ];




    const IconFont = createFromIconfontCN({
        scriptUrl: '//at.alicdn.com/t/c/font_3621294_q6pex0cx8k.js',
    });
    return (
        <div className='Header'>
            <Row>
                <Col className='Header-content' xs={12} sm={12} md={8} lg={8} xl={10}>
                    <IconFont spin='true' style={{ fontSize: "20px" }} type="icon-xiongmao" />

                    <span className='Header-logo'>
                        <a href="/home"></a>
                        {/*  */}

                    </span>
                    <IconFont spin='true' style={{ fontSize: "20px" }} type="icon-xiongmao" />

                    <span className="Header-txt">/(-(oo)-)\!!!</span>
                </Col>
                <Col xs={12} sm={12} md={16} lg={16} xl={12}>
                    <Menu mode="horizontal" onClick={handleClik}>
                        {/* <Menu.Item key="0" onClick={handleClik}>
                            <Icon type="home" />
                            
                        </Menu.Item>
                        <Menu.Item key="1" onClick={handleClik}>
                            <Icon type="vedio" />
                            
                        </Menu.Item>
                        <Menu.Item key="2" onClick={handleClik}>
                            <Icon type="smile" />
                            
                        </Menu.Item> */}
                        {
                            navArray.map((item) => {
                                return (
                                    <Menu.Item key={item.Id} >
                                        <IconFont type={item.icon} />
                                        {item.typeName}
                                    </Menu.Item>
                                )
                            })
                        }
                    </Menu>
                </Col>

            </Row>
        </div>


    )




}

export default Header;