import Header from '../../components/publicHeader/Header.js'
import "./Detailed.css"
import { Row, Col, Affix, Breadcrumb } from 'antd'
import { PlaySquareOutlined, UsergroupAddOutlined, CalendarOutlined } from '@ant-design/icons';
// import ReactMarkdown from 'react-markdown'
// import remarkGfm from 'remark-gfm'
// import rehypeRaw from 'rehype-raw'// html
// import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter' //
