import Author from '../../components/Author/Author.js'
import Advert from '../../components/Advert/Advert.js'
import Footer from '../../components/Footer/Footer.js'
import MarkNav from 'markdown-navbar';
import "../../../node_modules/markdown-navbar/dist/navbar.css"
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { useLocation } from 'react-router-dom';


import marked from 'marked'
import hljs from "highlight.js";
import 'highlight.js/styles/monokai-sublime.css';



import ServicePath from "../../config/apiUrl.js" //   


function Detailed(props) {
    const [articleContent, setArticleContent] = useState('');
    const [htmlContent, sethtmlContent] = useState('');


    const renderer = new marked.Renderer();
    marked.setOptions({
        renderer: renderer,
        gfm: true,
        pedantic: false,
        sanitize: false,
        tables: true,
        breaks: false,
        smartLists: true,
        smartypants: false,
        highlight: function (code) {
            return hljs.highlightAuto(code).value;
        }
    });


    const location = useLocation();
    const searchParams = new URLSearchParams(location.search);
    const itemId = searchParams.get('id');
    console.log(itemId);
    // console.log(servicePath.getArticleById);
    useEffect(() => {
        let id = itemId;
        axios.get(ServicePath.getArticleById + id).then(res => {
            console.log('---->', res.data);
            // console.log(res.data.data[0].article_content);
            setArticleContent(res.data.data[0].article_content);
            sethtmlContent(marked(res.data.data[0].article_content));//MarkdownHTML
        }).catch((err) => {
            console.log(err)
        })
    }, [itemId])


    // console.log(markdown);
    // function getinfoById(itemId) {
    //     let id = itemId;
    //     axios.get('http://127.0.0.1:7001/default/getArticleById/' + id).then(res => {
    //         console.log('---->', res.data.data.introduce);
    //         // markdown = res.data.data;
    //         // setMylist(res.data.data);
    //         // return res.data.data.introduce;
    //     }).catch((err) => {
    //         console.log(err)
    //     })
    //     // return res.data.data;
    // }

    // markdown = getinfoById(itemId)
    // 

    return (
        <div>
            <Header>
                {/* <title>Home</title> */}
            </Header>
            <Row className='main' type='float' justify="center" >
                <Col className='left' xs={0} sm={0} md={4} lg={0} xl={3}>
                    <Affix offsetTop={5}>
                        <div className="detailed-nav comm-box">
                            <div className="nav-title"></div>
                            <MarkNav
                                className="article-menu"
                                source={htmlContent}
                                ordered={false}
                            />
                        </div>
                    </Affix>

                </Col>
                <Col className="center" xs={18} sm={18} md={8} lg={13} xl={13}  >
                    {/*  */}
                    <div className="bread-div">
                        <Breadcrumb>
                            <Breadcrumb.Item><a href="/bloghome"></a></Breadcrumb.Item>
                            <Breadcrumb.Item><a href="/List"></a></Breadcrumb.Item>
                            <Breadcrumb.Item>xxxx</Breadcrumb.Item>
                        </Breadcrumb>
                    </div>
                    <div>
                        <div className='detailed-title'>
                            React--blog!...
                        </div>
                        <div className="detailed-icon">
                            <span><CalendarOutlined />2023-06-28</span>
                            <span><PlaySquareOutlined /></span>
                            <span><UsergroupAddOutlined /> ---</span>
                        </div>
                        <div className='detailed-content'
                            dangerouslySetInnerHTML={{ __html: htmlContent }}
                        >

                            {/* markdown */}

                            {/* <div className="detailed-content" > */}
                            {/* <ReactMarkdown className='markdown-body'
                                children={articleContent}
                                remarkPlugins={[remarkGfm, { singleTilde: false }]}
                                rehypePlugins={[rehypeRaw]}
                                skipHtml={false}
                                components={{
                                    code({ node, inline, className, children, ...props }) {
                                        const match = /language-(\w+)/.exec(className || '')
                                        return !inline && match ? (
                                            <SyntaxHighlighter
                                                children={String(children).replace(/\n$/, '')}
                                                style={duotoneEarth}
                                                language={match[1]}
                                                PreTag="div"
                                                {...props}
                                            />
                                        ) : (
                                            <code className={className} {...props}>
                                                {children}
                                            </code>
                                        )
                                    }
                                }}

                            ></ReactMarkdown> */}

                            {/* </div> */}

                        </div>
                    </div>
                </Col>

                <Col className="right" xs={6} sm={6} md={8} lg={5} xl={5}>
                    {/*  */}

                    <></>
                    <Author />
                    <Advert />

                </Col>
            </Row>
            <Footer></Footer>

        </div>
    )
}


export default Detailed;