import Header from '../../components/publicHeader/Header.js'
import "./BlogHome.css"
import { List } from 'antd'
import { Row, Col } from 'antd'
import React, { useState } from 'react'
import { PlaySquareOutlined, UsergroupAddOutlined, CalendarOutlined } from '@ant-design/icons';
import Author from '../../components/Author/Author.js'
import Advert from '../../components/Advert/Advert.js'
import Footer from '../../components/Footer/Footer.js'
import axios from 'axios'
import { useEffect } from 'react'
import ServicePath from '../../config/apiUrl.js'



function BlogHome() {

    const [mylist, setMylist] = useState(
        [//
            { title: ' opencv + ', content: ' opencv,  USBwindows STM32  ' },
            { title: 'CSS', content: '## CSSCSS...bugCSS <p> </p> ### ' },
            { title: 'opencv -------', content: 'opencvGoogleandlowOpenCVOpen Source Computer Vision LibraryOpenCVBSDLinuxWindowsAndroidMac OS C  C++ PythonRubyMATLAB' },
            { title: '00....', content: '000018K' },
        ]

    )
    useEffect(() => {
        axios.get(ServicePath.getArticleList).then(res => {
            console.log('---->', res.data);
            setMylist(res.data.data);
        }).catch((err) => {
            console.log(err)
        })
    }, [])

    // function getinfo() {

    //     axios.get('http://127.0.0.1:7001/default/ArticleList', {
    //         headers: {
    //             'Access-Control-Allow-Origin': 'http://localhost:3000'
    //         }
    //     }).then(res => {
    //         const persons = res.data;
    //         console.log(persons);
    //     }).catch(error => {
    //         console.log(error);
    //     });
    // }

    // getinfo();

    return (
        <div>
            <Header>
                {/* <title>Home</title> */}
            </Header>
            <Row className='main' type='float' justify="center">
                <Col className="left" xs={24} sm={24} md={16} lg={18} xl={14}  >
                    <List
                        header={<div></div>}
                        itemLayout="vertical"
                        dataSource={mylist}
                        renderItem={item => (
                            <List.Item>
                                <div className="list-title">{item.title}</div>
                                <div className="list-icon">
                                    <span><CalendarOutlined />{item.addTime}</span>
                                    <span><PlaySquareOutlined />{item.typeName}</span>
                                    <span><UsergroupAddOutlined />{item.viewCount}</span>
                                </div>
                                <div className="list-context">{item.content}</div>
                            </List.Item>
                        )}
                    />
                </Col>
                <Col className="right" xs={0} sm={0} md={7} lg={5} xl={4}>
                    <Author></Author>
                    <Advert></Advert>
                </Col>
            </Row>
            <Footer></Footer>
        </div>
    )
}





export default BlogHome;