import '../Home/Home.css';
import Typewriter from '../../components/typewriter/typewriter.js';
import React, { useState } from 'react';
import { Button } from 'antd';
import "../../../node_modules/antd/dist/antd.min.css"


function Home() {
    const [activeIndex, setActiveIndex] = useState(0);

    function clickHandler(idx) {
        setActiveIndex(idx);
        console.log("tab" + idx);

    };

    return (
        <div className="Home">
            <div className='Home-container'>
                <div className='Home-title'></div>
                <div className='Home-content'>
                    <Button style={{ color: "#fff", fontSize: '15px', textShadow: '1px 2px 2px rgba(0,0,0,0.8)', borderRadius: '20px' }} type='primary' size='large'></Button>
                    <Typewriter />
                </div>
                <div className='Home-link-area'>
                    <div className="Home-link-content">
                        {['', '', '', '', 'My Github'].map((item, idx) => (

                            <li
                                key={item}
                                className={idx === activeIndex ? 'active' : 'item'}
                                onClick={() => clickHandler(idx)}
                            >
                                {item}
                            </li>
                        ))}
                    </div>
                </div>



                <div className='Home-foot'>
                    Copyright  2023  | | Theme By Specs
                    ICP13026439-1
                </div>


            </div>

        </div >
    );
}



export default Home;