import React, { useState } from 'react'
import { List, Breadcrumb } from 'antd'
import { Row, Col } from 'antd'
import Header from '../../components/publicHeader/Header.js'
import Author from '../../components/Author/Author.js'
import Footer from '../../components/Footer/Footer.js'
import Advert from '../../components/Advert/Advert.js'
import axios from 'axios'
import { useEffect } from 'react';

import { Link } from 'react-router-dom'; // react-router


import "./List.css"





function BlogList() {


    const [mylist, setMylist] = useState(
        [
            { title: ' opencv + ', content: ' opencv,  USBwindows STM32  ' },
            { title: 'CSS', content: '## CSSCSS...bugCSS <p> </p> ### ' },
            { title: 'opencv -------', content: 'opencvGoogleandlowOpenCVOpen Source Computer Vision LibraryOpenCVBSDLinuxWindowsAndroidMac OS C  C++ PythonRubyMATLAB' },
            { title: '00....', content: '000018K' },
        ]

    )


    useEffect(() => {
        axios.get('http://127.0.0.1:7001/default/ArticleList').then(res => {
            console.log('---->', res.data);
            setMylist(res.data.data);
        }).catch((err) => {
            console.log(err)
        })
    }, [])



    return (
        <div>
            <Header>
                {/* <title>Home</title> */}
            </Header>
            <Row className='main' type='float' justify="center">
                <Col className="left" xs={24} sm={24} md={16} lg={18} xl={14}  >
                    <div className="bread-div">
                        <Breadcrumb>
                            <Breadcrumb.Item><a href="/bloghome"></a></Breadcrumb.Item>
                            <Breadcrumb.Item></Breadcrumb.Item>
                        </Breadcrumb>
                    </div>
                    <List
                        header={<div></div>}
                        itemLayout="vertical"
                        dataSource={mylist}
                        renderItem={item => (
                            <List.Item>
                                <div className="list-title">
                                    <Link to={{ pathname: '/Detailed', search: `?id=${item.id}` }}>
                                        {item.title}

                                    </Link>
                                </div>
                                <div className="list-icon">
                                    <span>2019-06-28</span>
                                    <span> </span>
                                    <span> 5498</span>
                                </div>
                                <div className="list-context">{item.introduce}</div>
                            </List.Item>
                        )}
                    />
                </Col>

                <Col className="right" xs={0} sm={0} md={7} lg={5} xl={4}>
                    <Author></Author>
                    <Advert></Advert>
                </Col>
            </Row>
            <Footer></Footer>
        </div>
    )
}


export default BlogList;